import ActivitiesData from "../activities.json";
//import "./styles.css";



export const Profile = () => {

  const activities = [];

  for (const key in ActivitiesData) {
    const category = ActivitiesData[key];

    const html = <h3 key={category["title"]}>{category["title"]}</h3>;
    activities.push(html);

    for (const i in category["articles"]) {
      const activity = category["articles"][i];
      const imagePath = `/img/${activity["image"]}`; // テンプレートリテラルを使ってパスを作成

      //console.log(activity);
      const html =
        <a href={activity["link"]} target="_blank" key={activity["title"]} className="activity">
          <div className="image">
            <img src={imagePath} alt="" loading="lazy" />
          </div>
          <div className="info">
            <div className="title">{activity["title"]}</div>
            <div className="subtitle">{activity["subtitle"]}</div>
            <div className="media">{activity["media"]}</div>
          </div>
        </a>
      ;

      activities.push(html);
    }
  }

  // 現在年
  const current_date = new Date();
  const current_year = current_date.getFullYear();

  return (
    <div id="component-profile">
      <h3>経歴</h3>
      <div className="profile">Google News Lab ティーチング・フェロー、Data Graphics Inc. 代表。筑波大学卒業後、2010年東洋経済新報社に入社。2017年英国エディンバラ大学大学院（修士）修了。データ可視化を活用した報道コンテンツの開発、デザイン、記事執筆を行い、グッドデザイン賞などを受賞。スマートニュース メディア研究所を経て2022年よりGoogle News Labで報道機関や大学でデータの読み方や伝え方などのトレーニングを行う。著書に『データ思考入門』（講談社現代新書）など。</div>
      <div className="social-icons">
        <div className="social-icon">
          <a className="facebook" href="https://www.facebook.com/kazuki.ogiwara.1/" target="_blank"></a>
          <p>Facebook</p>
        </div>
        <div className="social-icon">
          <a className="twitter" href="https://twitter.com/kaz_ogiwara" target="_blank"></a>
          <p>X (Twitter)</p>
        </div>
        <div className="social-icon">
          <a className="note" href="https://note.com/kazukio" target="_blank"></a>
          <p>note</p>
        </div>
        <div className="social-icon">
          <a className="github" href="https://github.com/kaz-ogiwara" target="_blank"></a>
          <p>GitHub</p>
        </div>
        <div className="social-icon">
          <a className="linkedin" href="https://www.linkedin.com/in/和樹-荻原-9ba764122/" target="_blank"></a>
          <p>Linkedin</p>
        </div>
      </div>
      <div className="activities">{activities}</div>
      <div className="copyright">© {current_year} Kazuki OGIWARA</div>
    </div>
  );

};
