import React, { useState } from 'react';
import WorksData from "../works.json";


export const Works = () => {

  const works = [];

  // useStateフックを使って状態を管理
  const [isExpanded, setIsExpanded] = useState(false);

  // ボタンがクリックされたときに呼び出される関数
  const handleButtonClick = () => {
    setIsExpanded(!isExpanded);
  };

  for (const key in WorksData) {
    const workData = WorksData[key];

    const imageStyle = {
      backgroundImage: `url(./img/eyecatch_${key}.png)`
    }

    const work =
            <a href={workData["link"]} target="_blank" key={key} className="section">
              <div className="imageWrapper">
                <div className="image" style={imageStyle}></div>
              </div>
              <div className="info">
                <h3>{workData["title-jp"]}</h3>
                <p>{workData["link-text"]}</p>
              </div>
            </a>
    ;

    works.push(work);
  }

  return (
    <div id="component-works" className={isExpanded ? 'expand' : ''}>
      <div className="inner">{works}</div>
      <div className="bottom">
        <button onClick={handleButtonClick}></button>
      </div>
    </div>
  );

};
